import React, {useEffect, useRef, useState} from "react";
import {useParams} from "react-router-dom";
import {useAppDispatch } from "hooks/redux";
import { post } from "types/post";
import styles from "./PostPage.module.scss";
import axios from "axios";
import {commentsReducer} from "../../context/commentsReducer";

const Post = React.lazy(() => import("components/Post"));

const PostPage = () => {
  const { id } = useParams();
  const [post, setPost] = useState<post | null>(null);
  const dispatch = useAppDispatch();
  const postRef = useRef<any>();
  const { clearComments } = commentsReducer.actions;

  useEffect(() => {
    getPost();

    return () => {
      dispatch(clearComments());
    }
  }, []);

  const getPost = async () => {
    const res = await axios.post(`http://127.0.0.1:8000/api/post/${id}`)
    const data = res.data;
    setPost(data)
  }

  return (
    post ?
      <div className={styles.container} ref={postRef}>
        <Post
          data={post!}
          withComments
        />
      </div>
      :
      <></>
  );
}

export default PostPage;
